.profile-card {
    display: flex;
    align-items: center;
    padding: 10px;
    border-radius: 8px;
    background-color: #1c1c1e;
    margin-bottom: 15px;
    transition: box-shadow 0.3s;
    cursor: pointer;
  }
  
  .profile-card:hover {
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  }
  
  .profile-image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .profile-info {
    display: flex;
    flex-direction: column;
  }
  
  .profile-username {
    font-weight: bold;
    color: #ffffff;
    margin: 0;
  }
  
  .profile-reason, .profile-mutuals {
    font-size: 0.85rem;
    color: #a0a0a5;
    margin: 2px 0;
  }
  
  .follow-button {
    background-color: transparent;
    color: #0095f6;
    border: none;
    font-size: 0.9rem;
    font-weight: bold;
    cursor: pointer;
    padding: 0;
  }
  
  .follow-button:hover {
    text-decoration: underline;
  }
  